





















































	export default {
		data() {
			return {
                goods:{},
                reason:""   //退货原因
			}
		},
		methods: {
              applyChange(order){
             	uni.request({
             		url:this.config.url+"order/refund",
             		method:"POST",
             		data:{
             			token:this.token,
             			order_no:order,
             			refund_type:2,
             			refund_desc:this.reason,
             			refund_covers:""
             			
             		},
             		success: (res) => {
             			console.log("return",res)
             		}
             	})
             }
		},
		mounted() {
			uni.getStorage({
				key:"regoods",
				success:(res)=>{
					console.log("regoods",res)
					this.goods=res.regoods;
				}
			})
		}
	}
